import React, { ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, KeyboardEvent } from "react";
import styled, { StyledComponentProps } from "styled-components/macro";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = DefaultInputPropsType & {
    //  +       
    onChangeText?: (value: string) => void;
    onEnter?: () => void;
    error?: string;
};

export const InputText: React.FC<SuperInputTextPropsType> = ({
    type, //          
    onChange,
    onChangeText,
    onKeyPress,
    onEnter,
    error,

    ...restProps //       restProps
}) => {
    const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
        onChange && //    onChange
            onChange(e); //     ( onChange  )

        onChangeText && onChangeText(e.currentTarget.value);
    };

    const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
        onKeyPress && onKeyPress(e);

        e.key === "Enter" && //    Enter
            onEnter && //    onEnter
            onEnter(); //   
    };

    return (
        <InputTextWrapper>
            <StyledInput
                type={type}
                onChange={onChangeCallback}
                onKeyPress={onKeyPressCallback}
                {...restProps} //        (value   )
            />
            <StyledError>{error && <span>{error}</span>}</StyledError>
        </InputTextWrapper>
    );
};

const InputTextWrapper = styled.div`
    position: relative;
    width: 100%;
`;

const StyledInput = styled.input<StyledComponentProps<any, any, any, any>>`
    //   

    padding: 1rem 0.5rem;

    height: 20px;
    width: 100%;

    font-size: ${({ theme }) => theme.font.size.default};
    font-family: ${({ theme }) => theme.font.family.default};
  box-shadow: ${({ theme }) => theme.shadow[3]};
    border: 1px solid rgb(109, 109, 109);
`;
//    
const StyledError = styled.div`
    position: absolute;
    bottom: 1px;

    //   
    & > span {
        font-size: ${({ theme }) => theme.font.size.s1};
        color: red;
    }
`;
